import { h, Component } from 'preact';
import style from './capabilities';
import SkillIcon from './skill-icon';
import SkillDetail from './skill-detail';

export default class Capabilities extends Component {
	render() {
		return (
			<div class={style.capabilities}>
        <div class={'page-header ' + style.header}>How can we help you?</div>
				<h2>Skills to pay the bills</h2>
				<p>Lorem ipsum made of gypsum.</p>

				<div class={style.skillmenu}>
					<SkillIcon icon="code" name="Front End" target="#front-end" />
					<SkillIcon icon="terminal" name="Back End" target="#back-end" />
					<SkillIcon icon="mobile-alt" name="Mobile" target="#mobile" />
					<SkillIcon icon="cloud" name="Cloud" target="#cloud" />
					<SkillIcon icon="pen-square" name="Design" target="#design" />
				</div>

				<div class="jumbo">
					Excellence is not a skill, it's an attitude.
					<cite>Ralph Marston</cite>
				</div>

				<SkillDetail icon="code" anchor="front-end">
					<h2>Front End</h2>
					Hello. Here is where we detail.
				</SkillDetail>

				<SkillDetail icon="terminal" anchor="back-end">
					<h2>Back End</h2>
					Hello. Here is where we detail.
				</SkillDetail>

				<SkillDetail icon="mobile-alt" anchor="mobile">
					<h2>Mobile</h2>
					Hello. Here is where we detail.
				</SkillDetail>

				<SkillDetail icon="cloud" anchor="cloud">
					<h2>Cloud Services</h2>
					Hello. Here is where we detail.
				</SkillDetail>
	
				<SkillDetail icon="pen-square" anchor="design">
					<h2>Design</h2>
					Hello. Here is where we detail.
				</SkillDetail>

			</div>
		);
	}
}



